.list-secondary-user {
  margin: 0 0 9em 0;
  max-width: 578px;
  padding: 0;

  @breakpoint ($bp-med-home) {
    margin: 0 0 60px 0;
  }

  @breakpoint ($bp-large-home) {
    margin: 0 auto;
    max-width: 100%;
  }
}
.list_links-secondary-user {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
}

.list_link_item-secondary-user {
  @extend %type-small;
  font-weight: $font-weight-bold;
  margin-bottom: 0.666em;
}
