.list_links-social {
  list-style: none;
  margin: 0;
  padding: 0;
}

.list_link_item-social {
  display: inline-block;
  margin-right: 15px;

  @include breakpoint(1251px min-width) {
    margin-right: 0.83em;
  }

  &:last-of-type {
    margin-right: 0;
  }

  .list_link_item-social_title {
    position: absolute !important;
    overflow: hidden;
    width: 1px;
    height: 1px;
    padding: 0;
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
  }

  a {
    @extend %icon-transition;
    display: inline-block;

    &:hover {
      fill: $blue;
    }
  }
}
